.search-bar{
    position: relative;
    margin: 0 auto 27px auto;
    max-width: 360px;
    text-align: center;
}

.search-bar__input{
    padding: 10px 15px;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
}

.search-bar__input::-webkit-input-placeholder{ font-weight: 300; color: #919191; }
.search-bar__input::-ms-input-placeholder{ font-weight: 300; color: #919191; }
.search-bar__input::placeholder{ font-weight: 300; color: #919191; }

.search-bar__submit{
    position: absolute;
    top: 4px;
    right: 4px;
    padding: 6px 8px 3px 8px;
    border: none;
    background: none;
    cursor: pointer;
    opacity: 0.8;
}

.search-bar__submit:hover,
.search-bar__submit:focus{
    opacity: 1;
}
.capitals{
    padding-top: 20px;
    border-top: 1px solid #FFF;
}

.capitals__title{
    margin-bottom: 10px;
    font-size: 26px;
    font-weight: 700;
    color: #FFF;
}

.capitals__content{
    max-width: 330px;
    margin: 0 auto;
}

.capitals__column-wrapper{
    display: inline-block;
    width: 50%;
    vertical-align: top;
}

.capitals__table{
    width: 100%;
}

.capitals__table th,
.capitals__table td{
    padding: 4px 0;
}

.capitals__table th{
    text-align: left;
    font-weight: 300;
}

.capitals__table td{
    font-weight: 700;
}

.capitals__colMin,
.capitals__colMax{
    width: 30px;
}

.capitals__table--Side--Right td,
.capitals__table--Side--Right th{
    text-align: right;
}
.app{
    width: 100%;
    max-width: 420px;
    margin: 0 auto;
}

.header{
    margin-bottom: 20px;
}

.header__title{
    text-align: center;
    font-size: 40px;
    font-weight: 700;
    color: #FFF;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    padding: 20px;
    font: normal 14px Roboto, Arial, Helvetica, sans-serif;
    background: #a7a7a7;
    background: #FF7F00;
    background: -webkit-linear-gradient(top, #FF7F00, #ffba00) fixed;
    background: linear-gradient(to bottom, #FF7F00, #ffba00) fixed;
    background-repeat: no-repeat;
}

